import { useMemo } from "react";
import Loading from "components/common/loading/Loading";
import TableBasic from "components/common/TableBasic/TableBasic";
import ViewFilePdf from "components/ViewFilePdf/ViewFilePdf";
import i18n from "i18n/i18n";
import React, { useEffect, useState } from "react";
import useChiTietNganhDaoTao from "stores/admin/CTNganhDaoTaoStore/CTNganhDaoTaoStore";
import { Modal, ModalHeader } from "reactstrap";
import queryString from "query-string";
import CONFIG from "config/config";
import "./ViewChiTietNganhDaoTao.css";
import PdfEditor from "components/common/PdfEditor/PdfEditor";
import ViewModeHtml from "components/ViewModeHtml/ViewModeHtml";
import { useHistory } from "react-router";
import { RoutesString } from "pages/routesString";

const ViewChiTietNganhDaoTao = (props) => {
    const history = useHistory();
    const [isLoading, setIsLoading] = useState(false);
    const [
        stateChiTietNganhDaoTao,
        actionChiTietNganhDaoTao,
    ] = useChiTietNganhDaoTao();
    const [idChiTietNganhDaoTao, setIdChiTietNganhDaoTao] = useState(null);
    const [khoaTuyen, setKhoaTuyen] = useState(null);
    const [isOpenPreviewPdf, setIsOpenPreviewPdf] = useState(false);
    const [isOpenEditor, setIsOpenEditor] = useState(false);

    const toggle = () => setIsOpenPreviewPdf(!isOpenPreviewPdf);
    useEffect(() => {
        (async () => {
            setIsLoading(true);
            await actionChiTietNganhDaoTao.getChiTietNganhDaoTao({
                limit: 10000,
            });
            setIsLoading(false);
        })();
    }, []);
    const titleList = [
        { title: "ID", field: "id" },
        { title: i18n.t("MAJORS_CODE"), field: "nganhDaoTao.maNganhDaoTao" },
        { title: i18n.t("MAJORS"), field: "nganhDaoTao.ten" },
        { title: i18n.t("SCHOOL_YEAR"), field: "khoa" },
        {
            title: i18n.t("PROGRAM_NAME"),
            field: "nganhDaoTao.chuongTrinhDaoTao.ten",
        },
        {
            title: "Action",
            actionTitle: "Preview Pdf",
            handle: (item) => {
                setIdChiTietNganhDaoTao(item?.nganhDaoTao?.id);
                setKhoaTuyen(item?.khoa);
                setIsOpenPreviewPdf(true);
            },
        },
        {
            title: "Action#2",
            actionTitle: "Edit Pdf - Mode: html",
            handle: (item) => {
                history.push(
                    RoutesString.ADMIN_CHUONG_TRINH_DAO_TAO_HTML_MODE.replace(
                        ":id",
                        item?.nganhDaoTao?.id
                    ).replace(":khoa", item?.khoa)
                );
            },
        },
        // {
        //     title: "Action#3",
        //     actionTitle: "Edit Pdf - Mode: UI",
        //     handle: (item) => {
        //         history.push(
        //             RoutesString.ADMIN_CTDT_EDITOR_MODE_UI.replace(
        //                 ":id",
        //                 item?.nganhDaoTao?.id
        //             ).replace(":khoa", item?.khoa)
        //         );
        //     },
        // },
    ];

    return (
        <div className="viewchitietnganhdaotao__wrapper">
            <div className="viewchitietnganhdaotao__body">
                <div className="viewchitietnganhdaotao__top">
                    <h4>{i18n.t("LIST_MAJOR_DETAIL")}</h4>
                </div>
                <div className="viewchitietnganhdaotao__content">
                    {isLoading ? (
                        <div className="loading">
                            <Loading />
                        </div>
                    ) : (
                        <TableBasic
                            titleList={titleList}
                            data={stateChiTietNganhDaoTao?.data}
                        />
                    )}
                </div>
                <div className="viewchitietnganhdaotao__view-pdf">
                    <Modal
                        className="overlay-pdf"
                        size="lg"
                        isOpen={isOpenPreviewPdf}
                        toggle={toggle}
                    >
                        <ModalHeader toggle={toggle}>PDF</ModalHeader>
                        <ViewFilePdf
                            url={`${
                                CONFIG.API.EXPORT_PDF
                            }?${queryString.stringify({
                                nganhDaoTao: idChiTietNganhDaoTao,
                                khoa: khoaTuyen,
                            })}`}
                        />
                    </Modal>
                </div>
                {/* <div className="viewchitietnganhdaotao__view-pdf">
                    <Modal
                        className="overlay-pdf"
                        size="lg"
                        isOpen={isOpenEditor}
                        toggle={() => setIsOpenEditor(!isOpenEditor)}
                    >
                        <ModalHeader
                            toggle={() => setIsOpenEditor(!isOpenEditor)}
                        >
                            Editor
                        </ModalHeader>
                        <ViewModeHtml
                            nganhDaoTao={idChiTietNganhDaoTao}
                            khoa={khoaTuyen}
                        />
                    </Modal>
                </div> */}
            </div>
        </div>
    );
};

export default ViewChiTietNganhDaoTao;
